
import XHeader from 'vux/src/components/x-header/index.vue'
import Toast from 'vux/src/components/toast/index.vue'
import InlineLoading from 'vux/src/components/inline-loading/index.vue'

import SelectTab from "components/SelectTab/selectTab";
export default {
components: {
XHeader,
Toast,
SelectTab,
InlineLoading
},
data() {
return {
noData: false,
TabDefault1:{
key: 'GXY,TNB',
val: '全部类型'
},
TabDefault2:{
key: 10000,
val: '全部'
},
select1: [{
key: 'GXY,TNB',
val: '全部类型'
}, {
key: 'GXY',
val: '高血压'
}, {
key: 'TNB',
val: '糖尿病'
}, ],
select2: [{
key: 10000,
val: '全部'
}, {
key: 3,
val: '近三月'
}, {
key: 6,
val: '近半年'
}, {
key: 12,
val: '近一年'
}, {
key: 36,
val: '近三年'
}],
list: [


















],
isloading: false,
start: '',
pageIndex: 1,
pageSize: 15,
num: 10000, //查询月数 默认全部
type: 'GXY,TNB', //查询类型 默认全部
}
},
mounted() {
this.initdata();
},
methods: {
initdata() {
var myDate = new Date();
this.start = myDate.getFullYear() + "" + (myDate.getMonth() + 1);
this.$ajax.post('', {
'MSH.1': 'mbinfo',
'MSH.2': 'mbsflist',
'num': this.num,
'start': this.start,
'GRDAID':this.$root.loginInfo.grdaid,
'pageIndex': this.pageIndex,
'pageSize': this.pageSize,
'CHZBM':this.$root.loginInfo.CHZBM,
'type': this.type,
'token':this.$root.loginInfo.token,
}).then(data => {
this.$refs.scroll.finishPullUp();
this.$refs.scroll.finishPullDown();
if (typeof data != 'undefined') { //判断是否空对象
if (typeof data.rows != 'undefined' && data.rows.length > 0) {

if (this.pageIndex == 1) {
this.list = data.rows
} else {
this.list = this.list.concat(data.rows);
}
this.noData = false;
} else {
this.judge()
}
} else {
this.judge()
}
})
},
judge() { //无数据判断
if (this.pageIndex > 1) {
this.$vux.toast.text('没有更多了', 'bottom');
} else {
this.list = [];
this.noData = true;
}
},
pullingDown() {
this.pageIndex = 1;
this.initdata();
},
pullingUp() {
this.pageIndex++;
this.initdata();
},
cancerDetail(str, id) { //点击跳转
if (str == 'GXY') {
this.$router.push({
name: "HyperRecord",
params: {
ID: id
}
});
} else if (str == 'TNB') {
this.$router.push({
name: "DiabetesFollowUp",
params: {
ID: id
}
});
}
},
selChange1(val) { //筛选触发
this.type = val.key;
this.list.length = 0;
this.pageIndex = 1;
console.log(val.key);
this.initdata();
},
selChange2(val) {
this.num = val.key;
this.list.length = 0;
this.pageIndex = 1;
this.initdata();
console.log(this.num);
},
}
}



